"use client"

import type React from "react"

import { useCallback, useMemo, useRef, useState, useEffect } from "react"
import { AgGridReact } from "ag-grid-react"
import {
  AllCommunityModule,
  ModuleRegistry,
  type ColDef,
  type GridReadyEvent,
  type ValueFormatterParams,
} from "ag-grid-community"
import type { Book } from "@/lib/baserow/types"
import { Button } from "@/components/ui/button"
import { Eye } from "lucide-react"
import Link from "next/link"
import { useGetBooksQuery, baserowApi } from "@/lib/redux/services/baserowApi"
import { useAppDispatch } from "@/lib/redux/hooks"

// Register AG Grid modules
ModuleRegistry.registerModules([AllCommunityModule])

interface BookGridItem extends Omit<Book, 'author'> {
  author?: { name: string } | null
}

interface BookAgGridProps {
  books: BookGridItem[]
}

export function BookAgGrid({ books: initialBooks }: BookAgGridProps) {
  const dispatch = useAppDispatch()

  // Hydrate cache with server-side data on mount
  useEffect(() => {
    if (initialBooks.length > 0) {
      dispatch(baserowApi.util.upsertQueryData('getBooks', undefined, initialBooks as Book[]))
    }
  }, [dispatch, initialBooks])

  // Subscribe to the query for updates
  const { data: booksData } = useGetBooksQuery(undefined, {
    // We already have data from server or cache, no need to refetch immediately unless stale
    refetchOnMountOrArgChange: false,
  })

  const books = booksData || initialBooks

  const gridRef = useRef<any>(null)
  const [quickFilterText, setQuickFilterText] = useState("")

  // Column definitions with sorting, filtering
  const columnDefs = useMemo<ColDef[]>(
    () => [
      {
        field: "title",
        headerName: "Title",
        flex: 2,
        minWidth: 200,
        filter: "agTextColumnFilter",
        sortable: true,
        cellClass: "font-medium text-foreground",
      },
      {
        field: "author_name",
        headerName: "Author",
        flex: 1.5,
        minWidth: 150,
        filter: "agTextColumnFilter",
        sortable: true,
        valueGetter: (params: any) => params.data?.author_name || "Unknown",
      },
      {
        field: "published_year",
        headerName: "Year",
        width: 100,
        filter: "agNumberColumnFilter",
        sortable: true,
        valueFormatter: (params: ValueFormatterParams) => params.value || "N/A",
      },
      {
        field: "publisher_name",
        headerName: "Publisher",
        flex: 1.5,
        minWidth: 150,
        filter: "agTextColumnFilter",
        sortable: true,
        valueFormatter: (params: ValueFormatterParams) => params.value || "N/A",
      },
      {
        field: "place_of_publication",
        headerName: "Place",
        width: 140,
        filter: "agTextColumnFilter",
        sortable: true,
        valueFormatter: (params: ValueFormatterParams) => params.value || "N/A",
      },
      {
        field: "class_number",
        headerName: "Class #",
        width: 120,
        filter: "agTextColumnFilter",
        sortable: true,
        valueFormatter: (params: ValueFormatterParams) => params.value || "N/A",
      },
      {
        headerName: "Actions",
        width: 100,
        sortable: false,
        filter: false,
        cellRenderer: (params: any) => {
          const bookId = params.data?.id
          return (
            <div className="flex items-center gap-2 h-full">
              <Link href={`/books/${bookId}`}>
                <Button variant="ghost" size="sm" className="h-8 px-2">
                  <Eye className="h-4 w-4 mr-1" />
                  View
                </Button>
              </Link>
            </div>
          )
        },
        pinned: "right",
      },
    ],
    [],
  )

  // Default column properties
  const defaultColDef = useMemo<ColDef>(
    () => ({
      resizable: true,
      filterParams: {
        buttons: ["reset", "apply"],
      },
    }),
    [],
  )

  const onGridReady = useCallback((params: GridReadyEvent) => {
    params.api.sizeColumnsToFit()
  }, [])

  const onFilterTextChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setQuickFilterText(e.target.value)
  }, [])

  const exportToCsv = useCallback(() => {
    gridRef.current?.api.exportDataAsCsv({
      fileName: "library-books.csv",
    })
  }, [])

  return (
    <div className="mt-8 space-y-4">
      {/* Toolbar */}
      <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center justify-between">
        <div className="flex items-center gap-2">
          <input
            type="text"
            placeholder="Quick search all columns..."
            value={quickFilterText}
            onChange={onFilterTextChange}
            className="h-9 px-3 rounded-md border border-input bg-background text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 w-64"
          />
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm" onClick={exportToCsv}>
            Export CSV
          </Button>
          <span className="text-sm text-muted-foreground">
            {books.length} {books.length === 1 ? "book" : "books"}
          </span>
        </div>
      </div>

      {/* AG Grid */}
      <div
        className="ag-theme-custom rounded-lg border border-border overflow-hidden"
        style={{ height: 600, width: "100%" }}
      >
        <AgGridReact
          ref={gridRef}
          rowData={books}
          columnDefs={columnDefs}
          defaultColDef={defaultColDef}
          onGridReady={onGridReady}
          quickFilterText={quickFilterText}
          pagination={true}
          paginationPageSize={20}
          paginationPageSizeSelector={[10, 20, 50, 100]}
          animateRows={true}
          rowSelection="multiple"
          domLayout="normal"
          enableCellTextSelection={true}
          rowHeight={48}
          headerHeight={48}
        />
      </div>
    </div>
  )
}
